  components: {
    Swiper,
    SwiperSlide
  },
  data: () => ({
    swiperOption: {
      slidesPerView: 4,
      slidesPerGroup: 4,
      spaceBetween: 8,
      slidesPerColumn: 1,
      direction: 'horizontal',
      scrollbar: {
        el: '.swiper-scrollbar',
        dragClass: 'swiper-scrollbar-drag scroll-drag'
      },
      navigation: {
        nextEl: '.right-arrow',
        prevEl: '.left-arrow'
      },
      breakpoints: {
        1170: {
          slidesPerView: 4,
        },
        890: {
          slidesPerView: 3,
        },
        550: {
          slidesPerView: 2,
        }
      }
    }
  })
}
