<template>
  <div class="mobile-navigation">
    <div :class="isOpenNav ? '' : 'closed'" :style="`z-index: ${zIndexNav}`" class="nav-wrapper">
      <div class="items">
        <div class="item active">
          <div class="icon">
            <img ondragstart="return false" src="../../assets/nav-icons/mobile-icons/icon-1-active.svg" alt="">
          </div>
          <p class="item-title">Главная</p>
        </div>
        <div class="item">
          <div class="icon">
            <img ondragstart="return false" src="../../assets/nav-icons/mobile-icons/icon-2.svg" alt="">
          </div>
          <p class="item-title">Уроки</p>
        </div>
        <div class="item">
          <div class="icon">
            <img ondragstart="return false" src="../../assets/nav-icons/mobile-icons/icon-3.svg" alt="">
          </div>
          <p class="item-title">Расписание</p>
        </div>
        <div class="item">
          <div class="icon">
            <img ondragstart="return false" src="../../assets/nav-icons/mobile-icons/icon-4.svg" alt="">
          </div>
          <p class="item-title">Теория</p>
        </div>
        <div class="item">
          <div class="icon">
            <img ondragstart="return false" src="../../assets/nav-icons/mobile-icons/icon-5.svg" alt="">
          </div>
          <p class="item-title">Билеты</p>
        </div>
        <div class="item">
          <div class="icon">
            <img ondragstart="return false" src="../../assets/nav-icons/mobile-icons/icon-6.svg" alt="">
          </div>
          <p class="item-title">Уведомления</p>
        </div>
        <div class="item">
          <div class="icon">
            <img ondragstart="return false" src="../../assets/nav-icons/mobile-icons/icon-7.svg" alt="">
          </div>
          <p class="item-title">Оплаты</p>
        </div>
        <div class="item">
          <div class="icon">
            <img ondragstart="return false" src="../../assets/nav-icons/mobile-icons/icon-8.svg" alt="">
          </div>
          <p class="item-title">Настройки</p>
        </div>
        <div class="item">
          <div class="icon">
            <img ondragstart="return false" src="../../assets/nav-icons/mobile-icons/icon-9.svg" alt="">
          </div>
          <p class="item-title">Статистика</p>
        </div>
        <div class="item">
          <div class="icon">
            <img ondragstart="return false" src="../../assets/nav-icons/mobile-icons/icon-10.svg" alt="">
          </div>
          <p class="item-title">Сообщения</p>
        </div>
      </div>
      <div @click="toggleNavigation" class="btn-toggler">
        <span class="btn-toggler-item-1"></span>
        <span class="btn-toggler-item-2"></span>
      </div>
    </div>
    <div @click.self="isOpenNav = false" :class="isOpenNav ? 'blur-background-active' : ''" class="blur-background"></div>
  </div>
</template>

<script>
export default {
  name: "mobile-navigation",
  data: () => ({
    isOpenNav: false
  }),
  methods: {
    toggleNavigation () {
      this.isOpenNav = !this.isOpenNav
    }
  },
  computed: {
    zIndexNav() {
      return this.isOpenNav ? '10001' : '9997'
    }
  }
}
</script>

<style lang="scss" scoped>
  .blur-background {
    position: fixed;
    top: 0px;
    left: 0px;
    right: 0px;
    bottom: 0px;
    background: rgba(59, 58, 84, 0.5);
    backdrop-filter: blur(5px); 
    -webkit-backdrop-filter: blur(5px); 
    z-index: 10000;
    visibility: hidden;
    opacity: 0;
    transition-duration: .3s;
  }
  .blur-background-active {
    visibility: visible;
    opacity: 1;
  }
  @media screen and (min-width: 600px) {
    .mobile-navigation {
      display: none;
    }
  }
  .nav-wrapper {
    padding: 22px 10px 70px 10px;
    background: #404151;
    width: 100%;
    max-height: 260px;
    position: fixed;
    bottom: 0;
    left: 0;
    z-index: 9997;
    box-sizing: border-box;
    transition-duration: .2s;
    overflow: hidden;
    &.closed {
      max-height: 80px;
      padding: 0px 10px;
      & .item {
        &:nth-child(3) {
          opacity: 0;
        }
      }
      & .btn-toggler {
        background: linear-gradient(180deg, #404151 0%, #30313D 100%);
        box-shadow: 0px 15px 20px rgba(40, 41, 53, 0.6), inset 0px 1px 0px #5C5D6F;
        bottom: 10px;
        left: 50%;
        transform: translate(-50%, 0);
        &-item-2 {
          opacity: 1;
          transform: rotate(0deg)
        }
      }
    }
    & .items {
      display: flex;
      align-items: center;
      justify-content: space-between;
      flex-wrap: wrap;
    }
    & .btn-toggler {
      margin: 0px auto;
      width: 60px;
      height: 60px;
      border-radius: 50%;
      background: linear-gradient(180deg, #30313D 0%, #404151 100%);
      box-shadow: 0px 15px 20px rgba(40, 41, 53, 0.6), inset 0px 1px 0px #5C5D6F;
      cursor: pointer;
      display: flex;
      align-items: center;
      justify-content: center;
      position: absolute;
      bottom: 10px;
      left: 50%;
      transform: translate(-50%, 0);
      user-select: none;
      &-item-1 {
        width: 16px;
        height: 2px;
        display: block;
        background: #fff;
        position: absolute;
        transition-duration: .2s;
      }
      &-item-2 {
        width: 2px;
        height: 16px;
        display: block;
        opacity: 0;
        background: #fff;
        position: absolute;
        transition-duration: .2s;
        transform: rotate(90deg);
      }
    }
    & .item {
      width: calc(100% / 5);
      height: 71px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      margin: 6px 0px;
      transition-duration: .2s;
      user-select: none;
      &.active .item-title {
        color: #fff;
      }
      & .icon {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 24px;
        height: 22px;
        & img {
          width: 100%;
        }
      }
      &-title {
        color: #9596A9;
        margin: 11px 0px 0px 0px;
        font-family: 'SSans-Reg';
        font-size: 1.2rem;
        line-height: 1.5rem;
        text-align: center;
      }
    }
  }
</style>